@import "@/assets/style/mixins.scss"; @import "@/assets/style/variables.scss";













































.btb-layout-head {
  @include padding-sm;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: $color-black;

  .head_logo {
    @include margin-sm;
    @include radius-md;
    width: 3rem;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $color-primary;
    flex-shrink: 0;

    .logo_content {
      @include font-3xl-b;
      color: $color-black;
    }
  }

  .head_title {
    @include margin-sm;
    flex-grow: 1;

    .title_main {
      @include font-lg-b;
      color: $color-primary;
    }
    .title_sub {
      color: $color-gray-ll;
    }
  }

  .head_item {
    @include margin-md;
    @include font-lg;
    flex-shrink: 0;

    .header_button {
      color: $color-gray-ll;
      cursor: pointer;

      &:hover {
        color: $color-gray-lll;
      }
    }
  }
}
