@import "@/assets/style/mixins.scss"; @import "@/assets/style/variables.scss";







































































.btb-layout-aside {
  .btb-vue-list {
    @include padding-x(1rem);
    color: $color-gray-lll;

    .container_entry {
      @include padding-sm-y;
      cursor: pointer;

      &.entry-active,
      &:hover {
        color: $color-primary;
      }
    }
  }
}
