@import "@/assets/style/mixins.scss"; @import "@/assets/style/variables.scss";





































.btb-layout-foot{
  .foot_navbar {
    @include font-xl;
    @include padding-x(1rem);
    @include padding-y(0.5rem);
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $color-gray-l;

    .navbar_linkBtn {
      @include font-lg;
      color: $color-gray-d;

      &.linkBtn-paypal:hover {
        color: $color-paypal;
      }
      &.linkBtn-github:hover {
        color: $color-github;
      }
      &.linkBtn-npm:hover {
        color: $color-npm;
      }
      &.linkBtn-linkedin:hover {
        color: $color-linkedin;
      }
      &.linkBtn-wordpress:hover {
        color: $color-wordpress;
      }
      &.linkBtn-email:hover {
        color: $color-email;
      }

      & + .navbar_linkBtn {
        margin-left: 1rem;
      }
    }
  }

  .foot_copyright {
    @include font-xs;
    @include padding-md;
    color: $color-gray-lll;
    background-color: $color-gray-ddd;
    text-align: center;
  }
}
