@import "@/assets/style/mixins.scss"; @import "@/assets/style/variables.scss";

































































$aside-width: 300px;

.btb-layout {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  padding-top: 4rem;
  overflow-x: hidden;
  position: relative;

  .layout_head {
    position: fixed;
    flex-shrink: 0;
    top: 0;
    z-index: 1;
  }
  .layout_content {
    @include transition(margin-right);
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    @include media-breakpoint-up-md {
      padding: 0;
    }
  }
  .layout_foot {
    flex-shrink: 0;
  }
  .layout_aside {
    @include transition(right);
    position: absolute;
    overflow: auto;
    width: $aside-width;
    flex-shrink: 0;
    background-color: $color-mask-d;
    top: 4rem;
    right: 0;
    bottom: 0;

    &.aside-hidden {
      right: -100%;
    }

    @include media-breakpoint-up-md {
      background-color: $color-mask-l;
    }
  }
}
